'use client';

import { useEffect, useState } from 'react';

const DarkModeTest = () => {
  const [isDark, setIsDark] = useState(false);
  const [htmlClasses, setHtmlClasses] = useState('');
  const [localStorageTheme, setLocalStorageTheme] = useState('');

  useEffect(() => {
    const checkDarkMode = () => {
      const html = document.documentElement;
      const hasDarkClass = html.classList.contains('dark');
      const savedTheme = localStorage.getItem('theme') || 'none';
      
      setIsDark(hasDarkClass);
      setHtmlClasses(html.className);
      setLocalStorageTheme(savedTheme);
    };

    checkDarkMode();
    
    // Check every 500ms for changes
    const interval = setInterval(checkDarkMode, 500);
    
    return () => clearInterval(interval);
  }, []);

  const toggleDarkMode = () => {
    const html = document.documentElement;
    const currentTheme = localStorage.getItem('theme');
    
    if (html.classList.contains('dark') || currentTheme === 'dark') {
      html.classList.remove('dark');
      localStorage.setItem('theme', 'light');
      console.log('Switching to light mode');
    } else {
      html.classList.add('dark');
      localStorage.setItem('theme', 'dark');
      console.log('Switching to dark mode');
    }
    
    // Force re-render
    setTimeout(() => {
      const hasDarkClass = html.classList.contains('dark');
      setIsDark(hasDarkClass);
      setHtmlClasses(html.className);
      setLocalStorageTheme(localStorage.getItem('theme') || 'none');
    }, 100);
  };

  const forceDarkMode = () => {
    const html = document.documentElement;
    html.classList.add('dark');
    localStorage.setItem('theme', 'dark');
    setIsDark(true);
    setHtmlClasses(html.className);
    setLocalStorageTheme('dark');
    console.log('Forced dark mode');
  };

  const forceLightMode = () => {
    const html = document.documentElement;
    html.classList.remove('dark');
    localStorage.setItem('theme', 'light');
    setIsDark(false);
    setHtmlClasses(html.className);
    setLocalStorageTheme('light');
    console.log('Forced light mode');
  };

  return (
    <div className="fixed bottom-4 right-4 bg-white dark:bg-gray-800 p-4 rounded-lg shadow-lg border border-gray-200 dark:border-gray-700 z-50 max-w-xs">
      <div className="text-sm space-y-2">
        <p className="text-gray-900 dark:text-white font-semibold">Dark Mode Debug</p>
        <p className="text-gray-600 dark:text-gray-400">Status: <span className={isDark ? 'text-green-600' : 'text-red-600'}>{isDark ? 'ON' : 'OFF'}</span></p>
        <p className="text-gray-600 dark:text-gray-400 text-xs">HTML Classes: {htmlClasses}</p>
        <p className="text-gray-600 dark:text-gray-400 text-xs">localStorage: {localStorageTheme}</p>
        
        <div className="space-y-1">
          <button
            onClick={toggleDarkMode}
            className="w-full px-3 py-1 bg-blue-600 text-white rounded text-xs hover:bg-blue-700"
          >
            Toggle Dark Mode
          </button>
          <button
            onClick={forceDarkMode}
            className="w-full px-3 py-1 bg-gray-800 text-white rounded text-xs hover:bg-gray-900"
          >
            Force Dark
          </button>
          <button
            onClick={forceLightMode}
            className="w-full px-3 py-1 bg-gray-200 text-gray-800 rounded text-xs hover:bg-gray-300"
          >
            Force Light
          </button>
        </div>
      </div>
    </div>
  );
};

export default DarkModeTest; 